import {
  Component, OnInit, OnDestroy, ElementRef, Renderer, ViewContainerRef, forwardRef, Input,
  EventEmitter, Output, ViewChild, HostListener, TemplateRef
} from '@angular/core';
import {DropdownContainerComponent} from './dropdown-container.component';
import {DropdownState} from './dropdown.state';
import {ComponentLoader} from '../component-loader/component-loader.class';
import {Subscription} from 'rxjs/Subscription';
import {ComponentLoaderFactory} from '../component-loader/component-loader.factory';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from '@angular/forms';

export type ModelType = Object
  | Object[]
  | string
  | string[]
  | number
  | number[];

@Component({
  selector: 'ngx-multiselect',
  template: `
    <div class="input-group">
      <ng-content select=".input-group-btn"></ng-content>
      <ng-content select=".input-group-addon"></ng-content>
      <input class="form-control" [placeholder]="placeholder" [value]="displayValue()"
             autocomplete="off" readonly (click)="toggle()" [disabled]="disabled"
             autocapitalize="off" autocorrect="off" [class.ready]="!disabled && !readonly">
      <div class="input-group-btn">
        <button class="btn btn-default" (click)="clearSelection($event)" [disabled]="disabled || readonly">
          <i class="glyphicon glyphicon-remove"></i>
        </button>
      </div>
    </div>
    <i class="caret" (click)="toggle()"></i>
    <template #dropdownRef>
      <ul class="dropdown-menu dropdown-menu-right" [style.min-width.px]="dropdownMenuWidth" #dropdownMenu>
        <li *ngIf="multiple"><a (click)="selectAll($event)" class="action">Select All</a></li>
        <li *ngIf="multiple" class="divider"></li>
        <li *ngIf="allowSearch" class="search-container">
          <input class="form-control input-sm" placeholder="Search..." (keyup)="search($event)" autocapitalize="off"
                 autocorrect="off"></li>
        <li>
          <ul class="list-unstyled option-list">
            <li class="selectable" *ngFor="let item of optionList" [ngClass]="{'active' : markActive(item)}"><a
              (click)="selectValue(item)" [ngClass]="{'multiple': multiple}">
              <i class="glyphicon glyphicon-ok" *ngIf="multiple" [ngClass]="{'no-visible': !isSelected(item)}"></i>
              <span *ngIf="displayName">{{item[displayName]}}</span> <span *ngIf="!displayName">{{item}}</span> </a>
            </li>
          </ul>
        </li>
      </ul>
    </template>
  `,
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => MultiselectComponent),
    multi: true
  }],
  styles: [`
    .dropdown-menu {
      padding: 0;
    }

    .option-list {
      max-height: 300px;
      overflow-y: auto;
    }

    li a {
      cursor: pointer;
    }

    .option-list li a {
      display: block;
      padding: 3px 10px;
      clear: both;
      font-weight: 400;
      line-height: 1.42857143;
      color: #333;
      white-space: nowrap;
    }

    .option-list > li > a:focus, .option-list > li > a:hover {
      color: #262626;
      text-decoration: none;
      background-color: #f5f5f5;
    }

    li a.action {
      font-style: italic;
    }

    li a i {
      font-size: 12px;
    }

    .selectable {
      background-color: #fff;
    }

    .multiple {
      padding-left: 10px !important;
    }

    .no-visible {
      visibility: hidden;
    }

    .divider {
      margin: 2px;
    }

    .search-container {
      padding: 3px;
    }

    input.form-control {
      padding: 6px 20px 6px 12px;
    }

    input.form-control.ready {
      background-color: #fff;
      cursor: pointer;
    }

    .caret {
      position: absolute;
      top: 45%;
      right: 45px;
      z-index: 10;
    }

    .glyphicon {
      font-size: 12px;
    }

    :host {
      display: block;
      position: relative;
    }
  `
  ]
})
export class MultiselectComponent implements ControlValueAccessor, OnInit, OnDestroy {

  @Input()
  public placeholder: string;
  @Output()
  public change: EventEmitter<ModelType> = new EventEmitter();
  @Input()
  public displayName: string;
  @Input()
  public trackBy: string;
  public dropdownMenuWidth: number;
  public optionList: Object[] | number[] | string[];
  public onTouched: any = Function.prototype;
  public onChange: any = Function.prototype;
  @ViewChild('dropdownRef')
  public dropdownRef: TemplateRef<any>;
  @ViewChild('dropdownMenu')
  public dropdownMenu: ElementRef;

  private _multiple: boolean;
  private _isDisabled: boolean;
  private _required: boolean;
  private _readonly: boolean;
  private _allowSearch: boolean;
  private _options: Object[] | number[] | string[];
  private _value: any;
  private _dropdown: ComponentLoader<DropdownContainerComponent>;
  private _subscriptions: Subscription[] = [];
  private isOpen: boolean;

  constructor(private _elementRef: ElementRef,
              private _renderer: Renderer,
              private _viewContainerRef: ViewContainerRef,
              private _cis: ComponentLoaderFactory,
              private _state: DropdownState) {
    // create dropdown component loader
    this._dropdown = this._cis
      .createLoader<DropdownContainerComponent>(this._elementRef, this._viewContainerRef, this._renderer)
      .provide({provide: DropdownState, useValue: this._state});
  }

  ngOnInit() {
    this._subscriptions.push(this._state
      .toggleClick.subscribe((value: any) => {
        this.toggle(value);
      }));
    this.placeholder = this.placeholder || '';
  }

  ngOnDestroy() {
    for (let sub of this._subscriptions) {
      sub.unsubscribe();
    }
    this._dropdown.dispose();
  }

  writeValue(v: any) {
    this._value = v;
  }

  registerOnChange(fn: any) {
    this.onChange = fn;
  }

  registerOnTouched(fn: any) {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean) {
    this.disabled = isDisabled;
  }

  @Input()
  get multiple() {
    return this._multiple;
  }

  set multiple(value: any) {
    this._multiple = this.coerceBooleanProperty(value);
  }

  @Input()
  get disabled() {
    return this._isDisabled;
  }

  set disabled(value: any) {
    this._isDisabled = this.coerceBooleanProperty(value);
  }

  @Input()
  get required() {
    return this._required;
  }

  set required(value: any) {
    this._required = this.coerceBooleanProperty(value);
  }

  @Input()
  get readonly() {
    return this._readonly;
  }

  set readonly(value: any) {
    this._readonly = this.coerceBooleanProperty(value);
  }

  @Input()
  get allowSearch() {
    return this._allowSearch;
  }

  set allowSearch(value: any) {
    this._allowSearch = this.coerceBooleanProperty(value);
  }

  @Input()
  get options() {
    return this._options;
  }

  set options(value: Object[] | number[] | string[]) {
    this._options = value;
  }

  clearSelection(event: any) {
    if (!this.disabled || !this.readonly) {
      this._value = null;
      this.onChange(this._value);
      this.change.emit(this._value);
    }
    event.stopPropagation();
  }

  displayValue() {
    if (this._value instanceof Array) {
      let count = this._value.length;
      if (this._value.length) {
        return count === 1
          ? (this.displayName ? this._value[0][this.displayName] : this._value[0])
          : count + ' selected';
      } else {
        return '';
      }
    } else if (this._value) {
      return this.displayName ? this._value[this.displayName] : this._value;
    } else {
      return '';
    }
  }

  toggle(value?: boolean) {
    if ((this.isOpen && this._dropdown.isShown) || value === false) {
      return this.hide();
    }
    return this.show();
  }

  show() {
    if (this.disabled || this.readonly) {
      return;
    }
    if (this._dropdown.isShown) {
      return;
    }
    this.syncOptions();
    this._dropdown
      .attach(DropdownContainerComponent)
      .to('body')
      .position({attachment: 'bottom right'})
      .show({
        content: this.dropdownRef,
        placement: 'bottom right'
      });
    this._state.isOpenChange.emit(true);
    this.isOpen = true;
    this.dropdownMenuWidth = this._elementRef.nativeElement.offsetWidth;
  }

  hide() {
    this._dropdown.hide();
    this._state.isOpenChange.emit(false);
    this.isOpen = false;
  }

  @HostListener('document:click', ['$event'])
  onDocumentClick(event: any) {
    if (event.button !== 2 && this._dropdown.isShown &&
      !this._elementRef.nativeElement.contains(event.target) &&
      !this.dropdownMenu.nativeElement.contains(event.target)) {
      this._state.toggleClick.emit(false);
    }
  }

  selectAll(event: any) {
    event.stopPropagation();
    this._value = (this.options as any[]).map((v: any) => {
      return v;
    });
    this.onChange(this._value);
    this.change.emit(this._value);
    this.hide();
  }

  search(event: any) {
    let value = event.target.value;
    if (value) {
      let options = this._options as any[];
      this.optionList = options.filter((v: any) => {
        let displayedValue = this.displayName ? v[this.displayName] : v;
        let regex = new RegExp('(' + value + ')', 'i');
        return (typeof displayedValue === 'string') ? regex.test(displayedValue) : false;
      });
    } else {
      this.syncOptions();
    }
  }

  selectValue(item: any) {
    if (this.multiple) {
      this._value = !(this._value instanceof Array) ? [] : this._value;
      let index = this.findIndex(this._value, item);
      if (index >= 0) {
        this._value.splice(index, 1);
      } else {
        this._value.push(item);
      }
    } else {
      this._value = item;
      this.hide();
    }
    this.onChange(this._value);
    this.change.emit(this._value);
  }

  isSelected(item: any) {
    let track = this.trackBy;
    if (this._value) {
      if (track) {
        return this.multiple
          ? this.exist(track, item)
          : this._value[track] === item[track];
      } else {
        return this.multiple
          ? (this._value instanceof Array) ? this._value.indexOf(item) >= 0 : false
          : this._value === item;
      }
    }
    return false;
  }

  markActive(item: any) {
    if (this.multiple === false) {
      return this.isSelected(item);
    }
  }

  exist(track: string, item: any) {
    let values = this._value;
    if (values instanceof Array) {
      return values.length ? (values.findIndex(function (v) {
          if (v[track] === item[track]) {
            return true;
          }
        })) >= 0 : false;
    } else {
      return values[track] === item[track];
    }
  }

  findIndex(values: any, item: any) {
    let track = this.trackBy;
    return values
      .findIndex((v: any) => {
        if (track && v[track] === item[track]) {
          return true;
        } else if (v === item) {
          return true;
        }
      });
  }

  private syncOptions() {
    this.optionList = (this.options as any[]).map((v: any) => {
      return v;
    });
  }

  private coerceBooleanProperty(value: any) {
    return value != null && '' + value !== 'false';
  }
}



